body {
  font-family: Montserrat, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

section {
  text-align: center;
  padding: 100px 0;
}

.link {
  text-decoration: none;
}

.container {
  width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

button.btn {
  color: #fff;
  cursor: pointer;
  height: 36px;
  background-color: #43b581;
  border: none;
  border-radius: 5px;
  margin-bottom: 60px;
  padding: 8px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button.btn:hover {
  background-color: #3fb072;
}

.cat-info {
  flex-wrap: wrap;
  gap: 60px 30px;
  margin-bottom: 60px;
  display: flex;
}

.cat-card {
  flex-basis: calc(25% - 22.5px);
  padding: 10px;
  position: relative;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

img {
  width: 100%;
  height: 100%;
  max-width: unset;
  object-fit: cover;
}

.info {
  justify-content: center;
  gap: 15px;
  display: flex;
}

.cat-info {
  font-size: 16px;
  font-style: italic;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  height: 32px;
  width: 200px;
  color: red;
  background: #fff;
  border: 2px solid #fff;
  margin: 0 auto;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:before {
  content: "";
  width: 0;
  height: 100%;
  background: red;
  animation: loading 10s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  content: "";
  width: 100%;
  height: 100%;
  text-align: center;
  color: #0ff;
  mix-blend-mode: difference;
  font-size: 24px;
  line-height: 32px;
  animation: percentage 10s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes loading {
  0% {
    width: 0;
  }

  100% {
    width: 100%;
  }
}

@keyframes percentage {
  0% {
    content: "0%";
  }

  5% {
    content: "5%";
  }

  10% {
    content: "10%";
  }

  20% {
